.splash{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    grid-gap: 2vh;
    justify-content: center;
    align-items: center;
    box-shadow: 1px 2px 5px var(--font-color);
    background: floralwhite;
    font-size:calc(16px + 6 * ((100vw - 320px) / 680));
    background-repeat: no-repeat;
    color: antiquewhite;
    background-position: right;
    height: 90vh;
    padding:1%;
    background-attachment: fixed;
}
.pentagon {
      position: relative;
      width: 54px;
      box-sizing: content-box;
      border-width: 50px 18px 0;
      border-style: solid;
      border-color: #a62e5ba8 transparent;
      transform: rotate(92deg);
    }
.pentagon:before {
      content: "";
      position: absolute;
      height: 0;
      width: 0;
      top: -85px;
      left: -18px;
      border-width: 0 45px 35px;
      border-style: solid;
      border-color: #663648 transparent
    }
  

#footer{
    text-align: center;
    background-color: whitesmoke;
    padding: 5px;
}
#footer > *{
        margin: 0;
}

.about{
    display:flex;
    align-items: center;
    justify-content: center;
    grid-gap:2vw;
    padding:3%;
}
.about > div:nth-child(1){
    flex: 3 1;
}
.about > div:nth-child(2){
    flex: 1 1;

}
.about > div:nth-child(1) > p {
    font-size:calc(16px + 6 * ((100vw - 320px) / 680));
}
.about > div:nth-child(2) > img {
    border-radius: 116px;
    border-top: 4px solid #f941418f;
    border-right: 4px solid #f941418f;
    border-left: 4px solid #f941418f;
}
